@import "../_core/all";
.home_slider {
    @include design_section($mautim,$mautrang);
    h1 {
        color: $mautrang;
        font-size: 1.5rem;
        letter-spacing: 2px;
        padding-bottom: 3rem; }
    .btn-more {
        color: $mautrang;
        background: $maucam;
        font-size: 2rem;
        padding: 0.5rem;
        &:hover {
            border-bottom: 2px solid darken($maucam, 50%); } } }
.home_services {
    @include design_section($maungoc,$mautrang);
    h2 {
        @include tag_before_after(); }
    img {
        max-width: 120px; } }
.home_ourteam {
    @include design_section($mauxam,darken($mautrang,100%));
    .rounded-circle {
        border-radius: 50%!important; }
    ul {
        list-style-type: none;
        display: flex;
        text-align: center;
        li {
            i {
                border-radius: 50%;
                color: $mautrang;
                background-color: #AAAAAA;
                height: 30px;
                width: 30px;
                margin: 3px;
                padding: 5px;
                font-size: 15px; } } }

    h2 {
        @include tag_before_after(); }
    h3 {
        color: darken($mautrang,100%); }
    h4 {
        color: #30bae7;
        font-size: 1rem; }
    img {
        max-width: 120px; } }

.home_skill {
    @include design_section($mautrang,darken($mautrang,100%));
    h2 {
        @include tag_before_after(); }
    h3 {
        color: darken($mautrang,100%); }
    img {
        max-width: 120px; } }
.home_portfolio {
    @include design_section($maucam,darken($mautrang,100%));
    h2 {
        @include tag_before_after(); }
    h3 {
        color: darken($mautrang,100%); }
    img {
        max-width: 120px; } }
