@mixin design_section($maunen: #f0f0f0, $mauchu: #000000) {
    min-height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: $maunen;
    color: $mauchu;
    border: $maunen; }

@mixin tag_before_after($mau: darken($mautrang,50%)) {
    position: relative;
    margin-bottom: 1rem;
    &:before,
    &:after {
        content: '';
        display: block;
        background: $mau;
        position: absolute;
        left: 50%;
        transform: translateX(-50%); }
    &:before {
        width: 120px;
        height: 5px;
        bottom: -8px; } }
